/**
 * ArrayBuffer
 * @param {String} imageUrl - URL
 * @param {String} baseUrl - URL
 * @returns {Promise<ArrayBuffer>} ArrayBuffer
 */
const fetchImageAsBuffer = async (imageUrl, baseUrl = window.location.origin) => {
  console.log(':', imageUrl);
  
  if (!imageUrl) {
    console.error('URL');
    return null;
  }
  
  // baseUrl
  const fullUrl = imageUrl.startsWith('/') 
    ? `${baseUrl}${imageUrl}` 
    : imageUrl;
  
  try {
    console.log(':', fullUrl);
    const response = await fetch(fullUrl, {
      headers: {
        'Cache-Control': 'no-cache',
        'Pragma': 'no-cache'
      }
    });
    
    if (!response.ok) {
      throw new Error(`: ${response.status} ${response.statusText}`);
    }
    
    const arrayBuffer = await response.arrayBuffer();
    console.log(':', arrayBuffer.byteLength, '');
    return arrayBuffer;
  } catch (error) {
    console.error(':', error);
    return null;
  }
};

/**
 * JSON
 * @param {String|Array} path - 
 * @returns {Array} 
 */
const parsePhotoPath = (path) => {
  if (!path) {
    return [];
  }
  
  // 
  if (Array.isArray(path)) {
    return path;
  }
  
  try {
    // JSON
    if (typeof path === 'string' && path.startsWith('[') && path.endsWith(']')) {
      const parsed = JSON.parse(path);
      return Array.isArray(parsed) ? parsed : [path];
    }
    
    // JSON
    return [path];
  } catch (error) {
    console.error(':', error);
    return [path];
  }
};

/**
 * ExcelExcelJS
 * @param {Array} data - 
 * @param {String} fileName - 
 * @param {Array} headers - 
 * @param {String} baseUrl - URL
 * @param {Function} onProgress - (, )
 */
export const exportToExcelWithImages = async (data, fileName, headers, baseUrl = window.location.origin, onProgress = null) => {
  console.log('=== exportToExcelWithImages  ===');
  console.log(':', data.length);
  
  if (!data || data.length === 0) {
    console.error('');
    return false;
  }

  try {
    // 
    fileName = fileName.replace(/[\\/:*?"<>|]/g, '_');
    const timestamp = new Date().toISOString().replace(/[-:.]/g, '').substring(0, 14);
    const fullFileName = `${fileName}_${timestamp}.xlsx`;
    
    // 
    const workbook = new ExcelJS.Workbook();
    workbook.creator = '';
    workbook.lastModifiedBy = '';
    workbook.created = new Date();
    workbook.modified = new Date();
    
    // 
    const worksheet = workbook.addWorksheet('');
    
    // 
    const columns = headers.map(header => ({
      header: header.text,
      key: header.field,
      width: header.isImage ? 20 : 15
    }));
    worksheet.columns = columns;
    
    // 
    worksheet.getRow(1).font = { bold: true };
    worksheet.getRow(1).alignment = { vertical: 'middle', horizontal: 'center' };
    
    // 
    console.log('...');
    
    // 
    const totalItems = data.length * (1 + headers.filter(h => h.isImage).length);
    let currentItem = 0;
    
    for (let rowIndex = 0; rowIndex < data.length; rowIndex++) {
      const row = data[rowIndex];
      
      console.log(` ${rowIndex + 1}/${data.length} ...`);
      
      // 
      const rowData = {};
      headers.forEach(header => {
        if (!header.isImage) {
          rowData[header.field] = row[header.field] || '';
        }
      });
      
      // 
      const excelRow = worksheet.addRow(rowData);
      
      // 
      excelRow.height = 80; // 
      
      // 
      currentItem++;
      if (onProgress && typeof onProgress === 'function') {
        onProgress(currentItem, totalItems);
      }
      
      // 
      for (const header of headers) {
        if (header.isImage) {
          const fieldName = header.field;
          const imagePath = row[fieldName]; // 
          
          if (imagePath) {
            console.log(` ${rowIndex + 1}  ${fieldName} : ${imagePath}`);
            
            // 
            try {
              const imageBuffer = await fetchImageAsBuffer(imagePath, baseUrl);
              
              if (imageBuffer) {
                // 
                const colIndex = headers.findIndex(h => h.field === fieldName) + 1;
                
                // 
                const cellRef = worksheet.getCell(rowIndex + 2, colIndex).address;
                
                // 
                const imageId = workbook.addImage({
                  buffer: imageBuffer,
                  extension: imagePath.split('.').pop().toLowerCase()
                });
                
                // 
                worksheet.addImage(imageId, {
                  tl: { col: colIndex - 1, row: rowIndex + 1 },
                  br: { col: colIndex, row: rowIndex + 1.9 },
                  editAs: 'oneCell'
                });
                
                console.log(` ${cellRef}`);
              } else {
                console.error(`: ${imagePath}`);
              }
            } catch (imageError) {
              console.error(`:`, imageError);
            }
          } else {
            console.log(` ${rowIndex + 1}  ${fieldName} `);
          }
          
          // 
          currentItem++;
          if (onProgress && typeof onProgress === 'function') {
            onProgress(currentItem, totalItems);
          }
        }
      }
    }
    
    console.log('Excel...');
    
    // Excel
    const buffer = await workbook.xlsx.writeBuffer();
    const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
    const url = window.URL.createObjectURL(blob);
    
    const link = document.createElement('a');
    link.href = url;
    link.download = fullFileName;
    link.click();
    
    // 
    window.URL.revokeObjectURL(url);
    
    console.log('Excel');
    return true;
  } catch (error) {
    console.error('Excel:', error);
    return false;
  }
};

/**
 * Excel()
 * @param {Array} data - 
 * @param {String} fileName - 
 * @param {Array} headers - 
 */
export const exportToExcel = async (data, fileName, headers) => {
  if (!data || data.length === 0) {
    console.error('');
    return false;
  }

  // 
  fileName = fileName.replace(/[\\/:*?"<>|]/g, '_');
  const timestamp = new Date().toISOString().replace(/[-:.]/g, '').substring(0, 14);
  const fullFileName = `${fileName}_${timestamp}.xlsx`;
  
  console.log(':', data.length);
  
  try {
    console.log('ExcelJSExcel...');
    
    // 
    const workbook = new ExcelJS.Workbook();
    workbook.creator = '';
    workbook.lastModifiedBy = '';
    workbook.created = new Date();
    workbook.modified = new Date();
    
    // 
    const worksheet = workbook.addWorksheet('');
    
    // 
    const columns = headers.map(header => ({
      header: header.text,
      key: header.field,
      width: 15
    }));
    worksheet.columns = columns;
    
    // 
    worksheet.getRow(1).font = { bold: true };
    worksheet.getRow(1).alignment = { vertical: 'middle', horizontal: 'center' };
    
    // 
    data.forEach(row => {
      const rowData = {};
      headers.forEach(header => {
        rowData[header.field] = row[header.field] || '';
      });
      worksheet.addRow(rowData);
    });
    
    // 
    console.log('Excel...');
    const buffer = await workbook.xlsx.writeBuffer();
    
    // Blob
    const blob = new Blob([buffer], { 
      type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
    });
    
    // 
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = fullFileName;
    console.log('');
    
    // 
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
    
    console.log('Excel');
    return true;
  } catch (error) {
    console.error('Excel:', error);
    console.log('CSV');
    // ExcelCSV
    exportToCSV(data, fileName, headers);
    return false;
  }
};

/**
 * CSV
 * @param {Array} data - 
 * @param {String} fileName - 
 * @param {Array} headers - 
 */
export const exportToCSV = (data, fileName, headers) => {
  // 
  const timestamp = new Date().toISOString().replace(/[-:.]/g, '').substring(0, 14);
  const csvFileName = `${fileName}_${timestamp}.csv`;
  
  // CSV
  let csvContent = '\uFEFF'; // BOM
  
  // 
  const headerRow = headers.map(h => `"${h.title}"`).join(',');
  csvContent += headerRow + '\r\n';
  
  // 
  data.forEach(item => {
    const row = headers.map(header => {
      const value = item[header.field];
      
      // nullundefined
      if (value === null || value === undefined) {
        return '';
      }
      
      // 
      if (header.type === 'number' || typeof value === 'number') {
        return value;
      }
      
      // 
      let cellValue = String(value);
      if (cellValue.includes(',') || cellValue.includes('"') || cellValue.includes('\n')) {
        cellValue = cellValue.replace(/"/g, '""'); // 
        cellValue = `"${cellValue}"`; // 
      }
      
      return cellValue;
    }).join(',');
    
    csvContent += row + '\r\n';
  });
  
  // 
  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
  const url = URL.createObjectURL(blob);
  const link = document.createElement('a');
  link.setAttribute('href', url);
  link.setAttribute('download', csvFileName);
  link.style.visibility = 'hidden';
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(url);
  
  console.log('CSV');
};

/**
 * 
 * @param {Array} records - 
 * @param {String} baseUrl - URL
 * @returns {Object} arrayBuffer
 */
export const prepareImageExportData = async (records, baseUrl = window.location.origin) => {
  const imageData = {};
  
  try {
    console.log(':', records.length);
    console.log('URL:', baseUrl);
    
    // 
    for (let index = 0; index < records.length; index++) {
      const record = records[index];
      imageData[index] = {
        beforeImage: null,
        afterImage: null
      };
      
      // 
      if (record.photo_path_before) {
        const photoPaths = parsePhotoPath(record.photo_path_before);
        if (photoPaths.length > 0) {
          const firstPhotoPath = photoPaths[0];
          try {
            const imageBuffer = await fetchImageAsBuffer(firstPhotoPath, baseUrl);
            if (imageBuffer) {
              imageData[index].beforeImage = {
                buffer: imageBuffer,
                extension: firstPhotoPath.split('.').pop().toLowerCase(),
                path: firstPhotoPath
              };
              console.log(` ${index} `);
            }
          } catch (error) {
            console.error(`:`, error);
          }
        }
      }
      
      // 
      if (record.photo_path_after) {
        const photoPaths = parsePhotoPath(record.photo_path_after);
        if (photoPaths.length > 0) {
          const firstPhotoPath = photoPaths[0];
          try {
            const imageBuffer = await fetchImageAsBuffer(firstPhotoPath, baseUrl);
            if (imageBuffer) {
              imageData[index].afterImage = {
                buffer: imageBuffer,
                extension: firstPhotoPath.split('.').pop().toLowerCase(),
                path: firstPhotoPath
              };
              console.log(` ${index} `);
            }
          } catch (error) {
            console.error(`:`, error);
          }
        }
      }
      
      console.log(` ${index + 1}/${records.length} `);
    }
    
    return imageData;
  } catch (error) {
    console.error(':', error);
    return {};
  }
};
